 // QR Code Generation Functions
const generateTLV = (tag, value) => {
  const encoder = new TextEncoder();
  const encoded = encoder.encode(value);
  const valueHex = Array.from(encoded)
    .map(b => b.toString(16).padStart(2, '0'))
    .join('');
  
  const lengthHex = (value.length).toString(16).padStart(2, '0');
  const tagHex = tag.toString(16).padStart(2, '0');
  return tagHex + lengthHex + valueHex;
};

const getBillDetailsForQRCode = (invoice) => {
  const sellerName = "Rabie Development Contracting Est.";
  const vatRegistrationNumber = "311321306600003";
  const invoiceDate = invoice.invoiceDetails.invoiceDate || '';

  const totalAmount = invoice.products
    .reduce((acc, product) => acc + (parseFloat(product.totalInclVAT) || 0), 0)
    .toFixed(2);
  
  const vatAmount = invoice.products
    .reduce((acc, product) => acc + (parseFloat(product.vatAmount) || 0), 0)
    .toFixed(2);

  const tlvEncoded = 
    generateTLV(1, sellerName) +
    generateTLV(2, vatRegistrationNumber) +
    generateTLV(3, invoiceDate) +
    generateTLV(4, totalAmount) +
    generateTLV(5, vatAmount);

  // Convert hex to Base64 without Buffer
  const hexToBase64 = (hexString) => {
    return btoa(hexString.match(/\w{2}/g).map(a => 
      String.fromCharCode(parseInt(a, 16))
    ).join(''));
  };

  return hexToBase64(tlvEncoded);
};
const handlePrintInvoice = async (invoiceId) => {
  try {
    setIsLoading(true);
    
    // Check if we already have the invoice data in state
    let invoiceToPrint = invoices.find(inv => inv._id === invoiceId);
    
    // If not found in state, fetch it
    if (!invoiceToPrint) {
      const res = await axios.get(`https://rabiebillsaudi.onrender.com/api/invoice/${invoiceId}`);
      invoiceToPrint = res.data;
    }
    
    const translatedGoods = await translateText(invoiceToPrint.products[0]?.natureOfGoods || '');
    const translatedDescription = await translateText(invoiceToPrint.products[0]?.productDescription || '');
    
    const printWindow = window.open('', '', 'height=600,width=800');
    const htmlContent = await getInvoiceHtmlContent(invoiceToPrint, translatedGoods, translatedDescription);
    
    printWindow.document.write(htmlContent);
    printWindow.document.close();
    printWindow.print();
  } catch (err) {
    console.error('Error printing invoice:', err);
  } finally {
    setIsLoading(false);
  }
};
  
